import React, { useEffect, useState } from "react";
import { useNavigate } from "react-router-dom";
import { useTranslation } from "react-i18next";
import { useSelector, useDispatch } from "react-redux";
import { RootState, AppDispatch, store } from "../../store";

import { toast } from "react-hot-toast";
import useUserStore from "../../store/useUserStore";
import storageUtils from "../../utils/storageUtils";

import Button from "../../components/Button";
import AppBar from "../../components/AppBar";
import TextInput from "../../components/TextInput";
import { showCustomToast } from "../../components/CustomToast";
import BasicTabs from "../../components/BasicTabs";
import MonthlyPlanList from "../../components/MonthlyPlanList";
import PointList from "../../components/PointList";
import VideoTicketList from "../../components/VideoTickList";
import ComicTicketList from "../../components/ComicTicketList";

import { EyeIcon, EyeSlashIcon } from "@heroicons/react/24/outline";
import { fetchPaymentHistory, fetchRecharge, setClCouponNum } from "../../store/rechargeSlice";
import { getFontClass, isLogin } from "../../utils/helper";
import PaymentGuide from "../../components/PaymentGuide";
import ExclusiveList from "../../components/ExclusiveList";

import CheckIcon from '@mui/icons-material/Check';
import { setRecommend2Pay } from "../../store/analyticsSlice";

const Recharge: React.FC = () => {
  const dispatch: AppDispatch = useDispatch();
  const navigate = useNavigate();
  const language = useSelector((state: RootState) => state.init.language);
  const { t } = useTranslation();
  const tabContent = [
    { key: "獨家", label: t("獨家"), content: <ExclusiveList /> },
    { key: "影視", label: t("影視"), content: <MonthlyPlanList /> },
    { key: "點數", label: t("點數"), content: <PointList /> },
    { key: "影片卷", label: t("影片卷"), content: <VideoTicketList /> },
    { key: "漫畫卷", label: t("漫畫卷"), content: <ComicTicketList /> },
    { key: "消費說明", label: t("消費說明"), content: <PaymentGuide /> },
  ];
  const [platform, setPlatform] = React.useState<string | null>(null);
  const userid = useSelector((state:RootState) => state.userInfo.userInfoData?.data.user_id);
  const clCouponNum = useSelector((state:RootState) => state.recharge.clCouponNum);
  const recharge = useSelector((state:RootState) => state.recharge.rechargeData);

  useEffect(() => {
    if (clCouponNum && userid && clCouponNum !== "" && userid !== "") {
      dispatch(fetchRecharge({cl_coupon_num: clCouponNum, user_id: userid}));
    } else {
      dispatch(fetchRecharge({}));
    }
  }, [dispatch, userid, clCouponNum]);

  useEffect(() => {
    const userAgent = navigator.userAgent.toLowerCase();
    if (
      userAgent.indexOf("iphone") > -1 ||
      userAgent.indexOf("ipod") > -1 ||
      userAgent.indexOf("ipad") > -1
    ) {
      setPlatform("iOS");
    } else if (userAgent.indexOf("android") > -1) {
      setPlatform("Android");
    } else {
      setPlatform("Unknown");
    }

    dispatch(setRecommend2Pay("pay"))
  }, []);

  useEffect(() => {
    return () => {
      dispatch(setClCouponNum(""));
    }
  }, [])

  return (
    <div>
      <AppBar title={t("儲值訂閱")} />
      <div className={`p-4 space-y-4 ${platform !== "iOS" ? "mt-6" : "mt-0"}`}>
        <h2 className={getFontClass(language, ["text-center", "font-bold"])}>
          {t("您最佳的儲值訂閱方案")}
        </h2>
        <div className="flex justify-center space-x-2">
          <span 
            className={getFontClass(`${language}_m`, ["text-white", "font-bold", "whitespace-nowrap"])}
          >
            <CheckIcon style={{ color: 'white', fontSize: 15 }} />
            {t("完整高清")}
          </span>
          <span 
            className={getFontClass(`${language}_m`, ["text-white", "font-bold", "whitespace-nowrap"])}
          >
            <CheckIcon style={{ color: 'white', fontSize: 15 }} />
            {t("每日更新")}
          </span>
          <span 
            className={getFontClass(`${language}_m`, ["text-white", "font-bold", "whitespace-nowrap"])}
          >
            <CheckIcon style={{ color: 'white', fontSize: 15 }} />
            {t("安全隱私")}
          </span>
        </div>
        {!isLogin(store.getState()) && (
          <div className="flex space-x-4">
            <Button
              className={getFontClass(`${language}_m`, ["button-full", "secondary", "mt-2"])}
              type="button"
              onClick={() => {
                navigate("/app/login");
              }}
            >
              {t("已經是會員?點此登入")}
            </Button>
            <Button
              className={getFontClass(`${language}_m`, ["button-full", "active", "mt-2", "w-1/3"])}
              type="button"
              onClick={() => {
                navigate("/app/login/register");
              }}
            >
              {t("快速註冊")}
            </Button>
          </div>
        )}
        <BasicTabs tabs={tabContent} />
        <div className="text-center text-sm">{t("付費金額為{{currency}}", {currency: recharge?.currency[language].symbol})}</div>
        <div className="text-center text-sm" style={{ marginTop: "0px" }}>
          {t("確認支付即表示您同意我們的使用條件")}
        </div>
      </div>
    </div>
  );
};
export default Recharge;
