import React, { useEffect, useRef, useState } from "react";
import { Dialog } from "@mui/material";
import {
  ChevronDownIcon,
  ChevronUpIcon,
  XMarkIcon,
  LockOpenIcon,
} from "@heroicons/react/24/outline";

import { RootState, AppDispatch } from "../../../store";
import { useDispatch, useSelector } from "react-redux";
import {
  login,
  logout,
  updatePoint,
  updateTicket,
} from "../../../store/userInfoSlice";
import { PaymentMethod } from "../../../type/rechargeType";
import { useTranslation } from "react-i18next";
import Modal from "..";
import { LoginParam } from "../../../type/userType";
import { useNavigate } from "react-router-dom";
import { decrypt, encrypt} from "../../../utils/encryptUtils";

import { decryptData } from "../../../utils/decryptUtils";
import { stringify } from "crypto-js/enc-utf8";
import { fetchPaymentHistory, setIsPaymentSuccess, setLastOrderId, setShowPaymentStatusModal } from "../../../store/rechargeSlice";
import CircularProgress from '@mui/material/CircularProgress';

interface PaymentMethodModalProps {
  open: boolean;
  param: { type: string; num: number; price: number; pid: string; useCoupon: boolean };
  onClose: () => void;
}

interface Submenu {
  name: string;
  short_desc?: string;
}

interface PaySet {
  [key: string]: {
    [ptid: string]: Submenu;
  };
}

const PaymentMethodModal: React.FC<PaymentMethodModalProps> = ({
  open,
  param,
  onClose,
}) => {
  const { t } = useTranslation();
  const navigate = useNavigate();
  const [mainAnchorEl, setMainAnchorEl] = useState<null | HTMLElement>(null);
  const [selectedOption, setSelectedOption] = useState<string | null>(null);
  const [selectedPtid, setSelectedPtid] = useState<string | null>(null);
  const [expandedOption, setExpandedOption] = useState<string | null>(null); // 控制展開收合
  const [isLoginRemindModalOpen, setIsLoginRemindModalOpen] = useState<boolean>(false);
  const [showLoading, setShowLoading] = useState<boolean>(false);

  const dispatch: AppDispatch = useDispatch();
  const payOption = useSelector(
    (state: RootState) => state.recharge.rechargeData?.paymentList
  );
  const [paySet, setPaySet] = useState<
    Record<string, Record<string, PaymentMethod>>
  >({});

  const init = useSelector((state: RootState) => state.init);
  const userInfo = useSelector((state: RootState) => state.userInfo);
  const clCouponNum = useSelector((state: RootState) => state.recharge.clCouponNum);
  const lastOrderId = useSelector((state: RootState) => state.recharge.lastOrderId) ?? "";
  const loginSession = useSelector((state: RootState) => state.userInfo.loginSession);

  const handleMainClick = (
    event: React.MouseEvent<HTMLButtonElement>,
    option: string
  ) => {
    setSelectedOption(option);
    setMainAnchorEl(event.currentTarget);
    setExpandedOption(expandedOption === option ? null : option);
  };

  const [payUrl, setPayUrl] = useState<string>("");

  const paylinkClickedTime = useRef<number | null>(null); // 使用 useRef 儲存時間

  useEffect(() => {
    const handleVisibilityChange = () => {
      if (
        document.visibilityState === "visible" &&
        paylinkClickedTime.current
      ) {
        const config: LoginParam = {
          username: loginSession?.username ?? "",
          password: loginSession?.password ?? "",
        };
        setShowLoading(true);
        dispatch(login(config))
          .then((result) => {
            dispatch(fetchPaymentHistory()).then((result) => {
              setTimeout(() => {
                const newOrderId = result?.payload?.pay_orders[0]?.order_id ?? "";
                console.log("new orderId: ", newOrderId);
                console.log("last orderId: ", lastOrderId);
                if (newOrderId !== lastOrderId) {
                  dispatch(setIsPaymentSuccess(true));
                  dispatch(setLastOrderId(result?.payload?.pay_orders[0]?.order_id));
                }
                dispatch(setShowPaymentStatusModal(true));
                onClose();
                setShowLoading(false);
                navigate("/app/member");
              }, 1000);
            });
          })
          .catch((error) => {
            console.error("retry login fail: ", error);
          });
        paylinkClickedTime.current = null; // 重置時間
      }
    };

    document.addEventListener("visibilitychange", handleVisibilityChange);
    return () => {
      document.removeEventListener("visibilitychange", handleVisibilityChange); // 清理事件
    };
  }, []);

  const handleSubmenuClick = (ptid: string) => {
    if (userInfo && Number(userInfo.userInfoData?.code) === 0) {
      paylinkClickedTime.current = Date.now();
      setSelectedPtid(ptid); // 記錄選中的 submenu
      const tokenParam = "QCAPI,123456";
      const userParam = userInfo.loginSession?.username ?? "";
      const sid = userInfo.loginSession?.sid ?? "";
      const pid = param.pid;
      //const GET_PARAM = {
      //  tokenparam: tokenParam,
      //  userparam: userParam,
      //  sid: sid,
      //  pid: pid,
      //  ptid: ptid,
      //  coupons_num: coupons_num
      //};
      //const GET_PARAM_ENCODE = encryptData(JSON.stringify(GET_PARAM)) ?? "";
      const coupons_num = clCouponNum;


      const data = {
        tokenParam: tokenParam,
        userParam: userParam,
        sid: sid,
        pid: pid,
        ptid: ptid,
        coupons_num: coupons_num,
      }
      
      const encrypted = encrypt(JSON.stringify(data));
      console.log("encrypted: ", encrypted);
      const decrypted = decrypt(encrypted);
      console.log("decrypted: ", decrypted);

      const url = `${init.init?.api_host}/pay/pay.php?data=${encrypted}`   
      //const url = `${init.init?.api_host}/pay/pay.php?tokenparam=${tokenParam}&userparam=${userParam}&sid=${sid}&pid=${pid}&ptid=${ptid}&coupons_num=${coupons_num}`;
      setPayUrl(url);
      return true;
    } else {
      setIsLoginRemindModalOpen(true);
      return false;
    }
  };
  

  const handleClose = () => {
    setMainAnchorEl(null);
  };

  const handlePurchase = () => {
    switch (param.type) {
      case "description":
        break;
      case "point":
        if (userInfo) {
          dispatch(
            updatePoint(
              Number(userInfo?.userInfoData?.data.point ?? 0) + param.num
            )
          );
        }
        break;
      case "video_online":
        break;
      case "video_download":
        if (userInfo) {
          dispatch(
            updateTicket({
              field: "video_download",
              value:
                Number(userInfo?.userInfoData?.data.ex_tickets.video_download) +
                param.num,
            })
          );
        }
        break;
      case "comic_online":
        break;
      case "comic_download":
        break;
    }
  };

  const handleHighLow = (value: PaymentMethod) => {
    let show = false;
    if (value.high) {
      if (param.price <= value.high) {
        show = true;
      } else {
        show = false;
      }
    }
    if (value.low) {
      if (param.price >= value.low) {
        show = true;
      } else {
        show = false;
      }
    }
    if (!value.high && !value.low) {
      show = true;
    }
    return show;
  };

  const handleHidePtids = (tmpPaySet: any) => {
    Object.entries(tmpPaySet).forEach(([key, value], index) => {
      Object.entries(value ?? {}).forEach(([pid, payMethod], index) => {
        if (payMethod.hideptids) {
          payMethod.hideptids.forEach((value: string) => {
            removePaymentToCategory(tmpPaySet, key, value);
          });
        }
      });
    });
  };
  const handlePidRange = (value: PaymentMethod) => {
    if (Array.isArray(value.pidrange)) {
      if (param.useCoupon || (value.pidrange && !value.pidrange?.includes(Number(param.pid)))) {
        return false;
      } else {
        return true;
      }
    } else {
      return true;
    }
  };

  const removePaymentToCategory = (
    tmpPaySet: any,
    category: string,
    pid: string
  ) => {
    if (tmpPaySet[category] && tmpPaySet[category][pid]) {
      delete tmpPaySet[category][pid];
      // 如果刪除後 category 變成空物件，可以選擇刪除 category
      if (Object.keys(tmpPaySet[category]).length === 0) {
        delete tmpPaySet[category];
      }
    }
  };

  const addPaymentToCategory = (
    tmpPaySet: any,
    category: string,
    key: string,
    value: PaymentMethod
  ) => {
    if (!tmpPaySet[category]) {
      tmpPaySet[category] = {};
    }
    tmpPaySet[category][key] = value;
  };

  useEffect(() => {
    if (payOption) {
      setPaySet({});
      const tmpPaySet = {};
      Object.entries(payOption).forEach(([key, value], index) => {
        const isCredit = [29,1056,1029,53,1060,1061,2,13,60,44,19,70,31].includes(Number(key));
        const isCn = [47,57,16,58,45,46,59,56,48,49,30,10,54,32,26,40,41].includes(Number(key));
        const isAtm = [22,23].includes(Number(key));
        const isTelecom = [33,1033,34,1034,35,36,1036,1055,7,6,37,55,4,5,3].includes(Number(key));
        const isConvenient = [11,12,14,15,51,52].includes(Number(key));
        const hideForMv = (param.type === "exclusive" || param.type === "subscription") && Number(value.vclose) !== 1
        const hideForExchage = (param.type === "comic" || param.type === "video") && Number(value.tclose) !== 1
        const hideForPoint = (param.type === "point") && Number(value.pclose) !== 1

        if (
          (hideForMv || hideForExchage || hideForPoint) &&
          handleHighLow(value) &&
          handlePidRange(value) &&
          !value.name.includes("台灣之星")
        ) {
          if (isCredit) {
            addPaymentToCategory(tmpPaySet, t("信用卡"), key, value);
          } else if (isCn){
            addPaymentToCategory(tmpPaySet, t("中國地區"), key, value);
          } else if (isAtm) {
            addPaymentToCategory(tmpPaySet, "ATM", key, value);
          } else if (isTelecom) {
            addPaymentToCategory(tmpPaySet, t("電信小額付費"), key, value);
          } else if (isConvenient) {
            addPaymentToCategory(tmpPaySet, t("便利商店代收"), key, value);
          }
        }
      });
      handleHidePtids(tmpPaySet);
      setPaySet(tmpPaySet);
    }

  }, [payOption, param]);

  return (
    <div>
      {!showLoading ? (
        <div>
          <Dialog open={open} onClose={onClose} fullWidth maxWidth="sm">
          <div
            className="p-4"
            style={{ backgroundColor: "black", color: "white" }}
          >
            {/* Header */}
            <div className="flex justify-center items-center pb-2 relative">
              <h2 className="text-lg font-bold">{t("付款方式")}</h2>
              <XMarkIcon
                className="w-5 h-5 text-gray-100 absolute right-0"
                onClick={onClose}
              />
            </div>
            {/* Dropdown List */}
            <div className="w-full">
              {[
                t("信用卡"),
                t("電信小額付費"),
                t("中國地區"),
                t("便利商店代收"),
                t("ATM"),
              ].map((cate, index) => (
                <div key={index} className="my-2 text-sm text-gray-300">
                  <button
                    onClick={(e) => handleMainClick(e, cate)}
                    className="w-full flex justify-between items-center py-2 border-b focus:outline-none"
                  >
                    <span>{cate}</span>
                    {expandedOption === cate ? (
                      <ChevronUpIcon className="w-5 h-5 text-gray-600" />
                    ) : (
                      <ChevronDownIcon className="w-5 h-5 text-gray-600" />
                    )}
                  </button>

                  {/* 展開submenu */}
                  {expandedOption === cate && (
                    <div className="pl-4 mt-2">
                      {Object.entries(paySet?.[cate] ?? {}).map(
                        ([ptid, submenu], index) => (
                          <a
                            id={ptid}
                            href={payUrl}
                            target="_blank"
                            key={index}
                            onClick={(e) => {
                              const canNavigate = handleSubmenuClick(ptid);
                              if (!canNavigate) {
                                e.preventDefault(); // 阻止默認跳轉
                              }
                            }}
                            className={`block py-1 px-2 rounded ${
                              selectedPtid === ptid
                                ? "bg-gray-600 text-white"
                                : "text-gray-300"
                            }`}
                            rel="noreferrer"
                          >
                            {`${submenu[`name_${init.language}` as keyof PaymentMethod] } ${
                              submenu.short_desc ? `(${submenu.short_desc})` : ""
                            }`}
                          </a>
                        )
                      )}
                    </div>
                  )}
                </div>
              ))}
            </div>

            {/* Bottom Hint */}
            <div className="flex items-center mt-4 text-sm text-gray-500">
              <LockOpenIcon className="text-red-600 w-5 h-5 mr-2" />
              <p>
                {t(
                  "採摘當前全球最嚴密標準SSL-128位元加密，帳單不會顯示任何成人訊息"
                )}
              </p>
            </div>
          </div>
          </Dialog>
          <Modal
            open={isLoginRemindModalOpen}
            content={[<div>{t("請先登入")}</div>]}
            onClose={() => setIsLoginRemindModalOpen(false)}
          />
        </div>
      ) : (
        <div style={{ backgroundColor: 'rgba(0, 0, 0, 0.50)' }} className="fixed inset-0 flex items-center justify-center z-99999">
          <CircularProgress size={60} color="error" />
        </div>
      )}
    </div>
  );
};

export default PaymentMethodModal;