import { useEffect, useState } from "react";
import { useSelector } from "react-redux";
import { RootState } from "../../store";

import Button from "../../components/Button";
import { colors } from "../../styles/colors";

import PaymentMethodModal from "../Modal/PaymentMethodModal";
import CouponModal from "../../components/Modal/CouponModal";
import { PayexCaseInfo } from "../../type/rechargeType";
import { useTranslation } from "react-i18next";

type ComicTicketListProps = {
  currencyType: string;
  name: string;
  num: number;
  bonus: number;
  price: number;
  pcLabel: string;
  handleClick: () => void;
};

function ComicTicketListCard({
  currencyType,
  name,
  num,
  price,
  bonus,
  pcLabel,
  handleClick,
}: ComicTicketListProps) {
  const {t} = useTranslation();
  return (
    <div
      className="flex flex-col items-center p-4 font-bold rounded-md"
      style={{ backgroundColor: colors.background }}
      onClick={handleClick}
    >
      <div className="flex justify-center items-end">
        <span className="text-xs">{name}</span>
        <span className="text-2xl px-0.5" style={{ lineHeight: "1" }}>
          {num}
        </span>
        <span className="text-xs">{t("張")}</span>
        {bonus && (
          <div className="flex justify-center items-end">
            <span className="text-red-500 text-xs">{t("+送")}</span>
            <span
              className="text-red-500 text-2xl px-0.5"
              style={{ lineHeight: "1" }}
            >
              {bonus}
            </span>
            <span className="text-red-500 text-xs">{t("張")}</span>
          </div>
        )}
      </div>
      <div className="flex justify-center items-end text-lg py-2">
        <span className="text-red-500">{currencyType}</span>
        <span>{price}</span>
      </div>
      {pcLabel && (
        <div className="flex justify-center items-center w-1/2 bg-red-500 text-yellow-200 py-2 rounded-md">
          <span>{pcLabel}</span>
        </div>
      )}
    </div>
  );
}

const ComicTicketList = () => {
  const {t} = useTranslation();
  const recharge = useSelector(
    (state: RootState) => state.recharge.rechargeData
  );
  const language = useSelector(
    (state: RootState) => state.init.language
  );
  const [payexCaseInfos, setPayexCaseInfos] = useState<PayexCaseInfo[]>([]);
  const [isModalOpen, setIsModalOpen] = useState(false);
  const [param, setParam] = useState({ type: "", num: 0, price: 0, pid: "", useCoupon: false });

  useEffect(() => {
    if (recharge && recharge.payexCaseInfos) {
      setPayexCaseInfos(
        recharge.payexCaseInfos.filter((item) => Number(item.comic_download) !== 0 || Number(item.comic_online) !== 0)
      );
    }
  }, [recharge]);

  const handleOpenModal = (
    type: string,
    num: number,
    price: number,
    pid: string,
    useCoupon: boolean
  ) => {
    setParam({ type, num, price, pid, useCoupon });
    setIsModalOpen(true);
  };

  const handleCloseModal = () => {
    setIsModalOpen(false);
  };

  const [isCouponOpen, setIsCouponOpne] = useState(false);
  const handleCloseCoupon = () => {
    setIsCouponOpne(false);
  };
  const handleOpenCoupon = () => {
    setIsCouponOpne(true);
  };
  return (
    <div className="flex flex-col gap-4">
      <Button className="button-full active mt-2" type="button" onClick={handleOpenCoupon}>
        {t("使用優惠券")}
      </Button>
      <div className="pt-1"></div>
      {payexCaseInfos.map((item: PayexCaseInfo) => (
        <ComicTicketListCard
          key={item.pc_id}
          name={item[`pc_name_${language}`] ?? ""}
          num={Number(item.et_ref_total)}
          bonus={
            Number(item.comic_download) !== 0
              ? Number(item.comic_download) - Number(item.et_ref_total)
              : Number(item.comic_online) - Number(item.et_ref_total)
          }
          currencyType={recharge!.currency[language].symbol}
          price={Math.round(Number(item[`amount_${language}`]))}
          pcLabel={item[`pc_label_${language}`] ?? ""}
          handleClick={() =>
            handleOpenModal(
              "comic",
              Number(item.et_ref_total),
              Number(item.amount),
              item.pc_id,
              Number(item.amount) < Number(item.refprice)
            )
          }
        />
      ))}
      {isModalOpen && (
        <PaymentMethodModal
          open={isModalOpen}
          onClose={handleCloseModal}
          param={param}
        />
      )}
      {isCouponOpen && (
        <CouponModal open={isCouponOpen} onClose={handleCloseCoupon} />
      )}
    </div>
  );
};
export default ComicTicketList;
