import {useEffect, useState} from "react";
import { Dialog, DialogContent, TextField } from "@mui/material";
import { XMarkIcon, TicketIcon } from "@heroicons/react/24/outline";

import { showCustomToast } from "../../../components/CustomToast";
import Modal from "../../Modal";
import { AppDispatch, RootState } from "../../../store";
import { useDispatch, useSelector } from "react-redux";
import { fetchMyCouponsList, fetchCoupon, fetchRecharge, setClCouponNum } from "../../../store/rechargeSlice";
import { Coupon } from "../../../type/rechargeType";
import { useTranslation } from "react-i18next";
import Button from "../../Button";

interface CouponModalProps {
  open: boolean;
  onClose: () => void;
  data?: any;
}

const CouponModal: React.FC<CouponModalProps> = ({ open, onClose, data }) => {
  const dispatch:AppDispatch = useDispatch();
  const { t } = useTranslation(); 
  const myCouponsList = useSelector((state: RootState) => state.recharge.myCouponsList);
  const userid = useSelector((state: RootState) => state.userInfo.userInfoData?.data.user_id);
  const init = useSelector((state: RootState) => state.init.init);
  const [couponIsOpen, setCouponIsOpen] = useState(false);
  const [currentIndex, setCurrentIndex] = useState(-1);
  const [selectedCoupons, setSelectedCoupons] = useState<Coupon | null>(null);
  const [couponCode, setCouponCode] = useState("");
  const [isLoginRemindModalOpen, setIsLoginRemindModalOpen] = useState(false);
  const handleCouponOpen = () => setCouponIsOpen(true);
  const handleCouponClose = () => setCouponIsOpen(false);

  useEffect(() => {
    dispatch(fetchMyCouponsList({
      user_id: userid,
      caseInfo: ""
    }));
  }, [dispatch]);
  
  const handleCouponCancel = () => {
    setCouponIsOpen(false);
  };
  const handleCouponsNumConfirm = () => {
    if (userid) {
      dispatch(fetchCoupon({
        jsonp: 1,
        act: "convert",
        coupons_num: couponCode,
      })).then((result: any) => {
        if (result.payload.code !== 0) {
          alert(result.payload.msg);
        } else {
          dispatch(fetchMyCouponsList({
            user_id: userid,
            caseInfo: ""
          }));
          showCustomToast("兌換成功！", "success");
        }
      });
    } else {
      setIsLoginRemindModalOpen(true);
    }
  }
  const handleConfirm = () => {
    if (typeof selectedCoupons === "object" && selectedCoupons !== null && Object.keys(selectedCoupons).length !== 0) {
      dispatch(fetchRecharge({cl_coupon_num: selectedCoupons.cl_coupon_num, user_id: userid}));
      dispatch(setClCouponNum(selectedCoupons.cl_coupon_num));
      setCouponIsOpen(false);
      showCustomToast("使用成功！", "success");
      onClose();
    }
  }

  useEffect(() => {
    console.log("myCouponsList: ", myCouponsList);
  }, [myCouponsList])


  return (
    <>
      <Dialog open={open} onClose={onClose} fullWidth>
        <div className="p-4 rounded-t-lg" style={{backgroundColor: "#272727"}}>
          {/* Header */}
          <div className="flex justify-center items-center" >
            <h2 className="text-white text-lg font-bold relative">{t("使用優惠券")}</h2>
            <XMarkIcon onClick={onClose} className="h-5 w-5 text-white absolute right-1"/>
          </div>

          <DialogContent style={{backgroundColor: "#272727", padding: "0px"}}>
            {/* Input Field */}
            <div className="mb-0.5">
              <TextField
                variant="outlined"
                fullWidth
                placeholder={t("請輸入優惠券代碼")}
                value={couponCode}
                onChange={(e) => setCouponCode(e.target.value)}
                InputProps={{
                  sx: {
                    color: 'white',               // 輸入文字顏色
                    '& .MuiOutlinedInput-notchedOutline': {
                      borderColor: '#272727',    // 自訂邊框色（例如 Tailwind 的 gray-700）
                      borderWidth: '2px'
                    },
                    '&.Mui-focused .MuiOutlinedInput-notchedOutline': {
                      borderColor: '#4F4F4F',    // 聚焦時邊框色（red-500）
                    },
                    '& .MuiInputBase-input': {
                      height: '2rem',      // 或用 padding 改善垂直對齊
                      padding: '0.5rem 1rem',
                      boxSizing: 'border-box',
                      backgroundColor: '#4F4F4F',
                      borderRadius: '8px'
                    }
                  },
                }}
              />
            </div>
            
            {/* Confirm Button */}
            <Button
              className={`button-full mt-1 w-full active mb-5`}
              type="button"
              onClick={handleCouponsNumConfirm}
            >
              {t("確定")}
            </Button>
            
            {/* Coupons List */}
            <h3 className="text-white text-sm font-semibold mb-2">{t("優惠特區")}</h3>
            <div className="space-y-4 w-full h-full">
              {(myCouponsList && typeof myCouponsList === "object" && Object.keys(myCouponsList).length > 0) ? (
                Object.values(myCouponsList).map((coupon:Coupon, index) => (
                  <div key={index} className="flex w-full h-[7.5rem] justify-between items-center p-1.5 rounded-lg" style={{backgroundColor: "#0a0f0f"}}>
                    <div className="flex items-center justify-center w-[70%] h-full">
                      <img src={require(".././../../images/Coupon.png")} alt={t("Coupon")} className="m-1 w-10 h-10" />
                      <div className="flex flex-col item-centers ml-2 w-full">
                        {coupon.ct_img_template !== "" && (
                          <img src={`https://iqqapp2.com/${coupon.ct_img_template}`} className="mt-[1px]"/>
                        )}
                          <h4 className="break-words text-red-500 text-sm font-bold">{coupon.ct_name}</h4>
                          <p className="text-gray-400 mb-2 wrap" style={{fontSize: "0.6rem"}}>{coupon.ct_description}</p>
                          <span className="text-xs mt-auto text-white">
                            {coupon.cl_eff_date_end ? `${t("有效至")}${coupon.cl_eff_date_end.split(" ")[0]}` : t("永久")}
                          </span> 
                      </div>
                    </div>
                    <div style={{borderLeft: "2px dashed #3C3C3C", height: "7rem"}}></div>
                    <div className={`mt-1 font-black text-secondary`} onClick={() => {
                      if (userid) {
                        setCurrentIndex(index);
                        handleCouponOpen();
                        setSelectedCoupons(coupon);
                      } else {
                        setIsLoginRemindModalOpen(true);
                      } 
                    }}>
                      {t("立即使用")}
                    </div>
                  </div>
                ))) : (
                  <div className="flex flex-col items-center pt-5 pb-[10rem]">
                    <img src={require(`../../../images/Coupon.png`)} className="text-red-500 w-50 h-50 mb-2" />
                    <span className="text-white text-center">{t("目前尚無回饋券")}</span>
                    <span className="text-white text-center">{t("請在上方輸入有效的優惠券代碼")}</span>
                  </div>
                )
              }
            </div>
          </DialogContent>
        </div>
        <Modal
          title={t("優惠券兌換")}
          open={couponIsOpen}
          onClose={handleCouponClose}
          content={[selectedCoupons?.ct_name,selectedCoupons?.ct_annotate,t("是否兌換?")]}
          buttons={
            [
              <Button
                className="button-full active mt-1 w-1/3"
                type="button"
                onClick={handleConfirm}
              >
                {t("確認")}
              </Button>,
              <Button
                className="button-full active mt-1 w-1/3"
                type="button"
                onClick={handleCouponClose}
              >
                {t("取消")}
              </Button>,
            ]
          }
        />
      </Dialog>
      <Modal
        open={isLoginRemindModalOpen}
        content={[<div>{t("請先登入")}</div>]}
        onClose={() => setIsLoginRemindModalOpen(false)}
      />
    </>
  );
};

export default CouponModal;
