import { useEffect, useState } from "react";
import { useSelector } from "react-redux";
import { RootState } from "../../store";

import Button from "../../components/Button";
import { colors } from "../../styles/colors";

import PaymentMethodModal from "../Modal/PaymentMethodModal";
import CouponModal from "../../components/Modal/CouponModal";
import { PayCaseInfo } from "../../type/rechargeType";
import { useTranslation } from "react-i18next";

type MonthlyPlanCardProps = {
  pcLabel: string;
  bonus: string;
  price: string;
  pointGift: string;
  amount: string;
  handleClick: () => void;
  currencyType: string;
};

function PointListCard({
  pcLabel,
  bonus,
  pointGift,
  amount,
  price,
  handleClick,
  currencyType
}: MonthlyPlanCardProps) {
  const { t } = useTranslation();
  return (
    <div
      className="flex flex-col items-center p-4 font-bold rounded-md"
      style={{ backgroundColor: colors.background }}
      onClick={handleClick}
    >
      <div className="flex justify-center items-end">
        <span className="text-3xl px-0.5" style={{ lineHeight: "1" }}>
          {amount}
        </span>
        <span className="text-sm">{t("點")}</span>
        {pointGift 
          ? (
            <span className="text-sm text-red-500">
              {t(`+送{{point}}影城點`, {point: pointGift} )}
            </span>
          )
          : Number(bonus) !== 0 
            ? (
              <span className="text-sm text-red-500">
                {t(`+送{{point}}影城點`, {point: bonus})}
              </span>
            )
            : (<></>)
        }
      </div>
      <div className="flex justify-center items-end text-lg py-2">
        <span className="text-red-500">{currencyType}</span>
        <span>{price}</span>
      </div>
      {pcLabel && pointGift && Number(pointGift) !== 0 && (
        <div className="flex justify-center items-center w-1/2 bg-red-500 text-yellow-200 py-2 rounded-md">
          <span>{pcLabel}</span>
        </div>
      )}
    </div>
  );
}

export default function PointList() {
  const {t} = useTranslation();
  const recharge = useSelector(
    (state: RootState) => state.recharge.rechargeData
  );
  const language = useSelector(
    (state: RootState) => state.init.language
  );
  const [isModalOpen, setIsModalOpen] = useState(false);
  const [param, setParam] = useState({ type: "", num: 0, price: 0, pid: "", useCoupon: false });
  const [payCaseInfos, setPayCaseInfos] = useState<PayCaseInfo[]>([]);

  useEffect(() => {
    if (recharge && recharge.payCaseInfos) {
      const data = [...recharge.payCaseInfos].sort(
        (a, b) => parseInt(a.pc_id) - parseInt(b.pc_id)
      );
      setPayCaseInfos(data);
    }
  }, [recharge]);

  const handleOpenModal = (
    type: string,
    num: number,
    price: number,
    pid: string,
    useCoupon: boolean
  ) => {
    setParam({ type, num, price, pid, useCoupon });
    setIsModalOpen(true);
  };

  const handleCloseModal = () => {
    setIsModalOpen(false);
  };

  const [isCouponOpen, setIsCouponOpne] = useState(false);
  const handleCloseCoupon = () => {
    setIsCouponOpne(false);
  };
  const handleOpenCoupon = () => {
    setIsCouponOpne(true);
  };
  return (
    <div className="flex flex-col gap-4">
      <Button className="button-full active mt-2" type="button" onClick={handleOpenCoupon}>
        {t("使用優惠券")}
      </Button>
      <div className="pt-1"></div>
      {payCaseInfos.map((item: PayCaseInfo) => (
        <PointListCard
          key={item.pc_id}
          currencyType={recharge!.currency[language].symbol}
          bonus={ (Number(item.point_a) + Number(item.point_b) - Number(item.amount)).toString() }
          pointGift={item.point_gift}
          amount={item.amount}
          price={Math.round(Number(item[`amount_${language}`])).toString()}
          pcLabel={item[`pc_label_${language}`] ?? ""}
          handleClick={() =>
            handleOpenModal(
              "point",
              Number(item.amount),
              Number(item.refprice),
              item.pc_id,
              Number(item.amount) < Number(item.refprice)
            )
          }
        />
      ))}
      {isModalOpen && (
        <PaymentMethodModal
          open={isModalOpen}
          onClose={handleCloseModal}
          param={param}
        />
      )}
      {isCouponOpen && (
        <CouponModal open={isCouponOpen} onClose={handleCloseCoupon} />
      )}
    </div>
  );
}
