import Button from "../../components/Button";
import { colors } from "../../styles/colors";
import { useEffect, useState } from "react";
import { useSelector } from "react-redux";
import { RootState } from "../../store";

import PaymentMethodModal from "../Modal/PaymentMethodModal";
import CouponModal from "../../components/Modal/CouponModal";
import { getFontClass } from "../../utils/helper";
import { useTranslation } from "react-i18next";

type MonthlyPlanCardProps = {
  currencyType: string; 
  pc_name: string;
  price: number;
  days: string;
  pcLabel: string;
  handleClick: () => void;
};

function MonthlyPlanCard({
  currencyType,
  pc_name,
  price,
  days,
  pcLabel,
  handleClick,
}: MonthlyPlanCardProps) {
  const planName = pc_name.replace(/（/g, "(").split("(");
  const whiteTxt = planName[0]?.replace(/<\/?span>|<b>/g, "");
  const redTxt = planName[1]?.replace(/<\/?span>|<\/?b>|\)|）/g, "");
  const {t} = useTranslation();
  const language = useSelector(
    (state: RootState) => state.init.language
  );

  return (
    <div
      className="flex flex-col items-center p-4 font-bold rounded-md"
      style={{ backgroundColor: colors.background }}
      onClick={handleClick}
    >
      <div className={getFontClass(`${language}_m`, ["flex", "justify-center"])}>
        <span>{whiteTxt}</span>
        (<span className="text-red-500">{redTxt}</span>)
      </div>
      <div className="flex justify-center items-end text-lg py-4">
        <span 
          className="text-red-500"
          style={{
            lineHeight: "1",
          }}
        >
          {currencyType}
        </span>
        <span
          className="px-0.5"
          style={{
            lineHeight: "1",
          }}
        >
          {Math.round(Number(price) / (Number(days) / 30))}
        </span>
        <span className="text-xs">{t("/月")}</span>
      </div>
      {pcLabel && (
        <div className="flex justify-center items-center w-1/2 bg-red-500 text-yellow-200 py-2 rounded-md">
          <span>{pcLabel}</span>
        </div>
      )}
    </div>
  );
}

export default function MonthlyPlanList() {
  const {t} = useTranslation();
  const recharge = useSelector(
    (state: RootState) => state.recharge.rechargeData
  );
  const language = useSelector(
    (state: RootState) => state.init.language
  );
  const [isModalOpen, setIsModalOpen] = useState(false);
  const [param, setParam] = useState({ type: "", num: 0, price: 0, pid: "", useCoupon: false });
  const [caseInfoKeys, setCaseInfoKeys] = useState<string[]>([]);

  useEffect(() => {
    if (recharge && recharge.payMonthCaseInfos) {
      setCaseInfoKeys(
        Object.keys(recharge.payMonthCaseInfos).sort(
          (a, b) => parseInt(a) - parseInt(b)
        )
      );
    }
  }, [recharge]);

  const handleOpenModal = (
    type: string,
    num: number,
    price: number,
    pid: string,
    useCoupon: boolean
  ) => {
    setParam({ type, num, price, pid, useCoupon });
    setIsModalOpen(true);
  };

  const handleCloseModal = () => {
    setIsModalOpen(false);
  };

  const [isCouponOpen, setIsCouponOpne] = useState(false);
  const handleCloseCoupon = () => {
    setIsCouponOpne(false);
  };
  const handleOpenCoupon = () => {
    setIsCouponOpne(true);
  };
  
  return (
    <div className="flex flex-col gap-4">
      <Button className="button-full active mt-2" type="button" onClick={handleOpenCoupon}>
        {t("使用優惠券")}
      </Button>
      <div className="pt-1"></div>
      {recharge?.payMonthCaseInfos.map((item, index) => (
        Number(item.pc_id) !== 92 && Number(item.site_type_id) === 1 && (
          <MonthlyPlanCard
            key={index}
            pc_name={item[`pc_name_${language}`] ?? ""}
            price={Number(item[`amount_${language}`])}
            days={item.days}
            pcLabel={item[`pc_label_${language}`] ?? ""}
            currencyType={recharge!.currency[language].symbol}
            handleClick={() =>
              handleOpenModal(
                "subscription",
                Number(item.days),
                Number(item.amount),
                item.pc_id,
                Number(item.amount) < Number(item.refprice)
              )
            }
          />
        )
      ))}
      {isModalOpen && (
        <PaymentMethodModal
          open={isModalOpen}
          onClose={handleCloseModal}
          param={param}
        />
      )}
      {isCouponOpen && (
        <CouponModal open={isCouponOpen} onClose={handleCloseCoupon} data={recharge?.payMonthCaseInfos}/>
      )}
    </div>
  );
}